import { _decorator, Component, Node, Vec3, Vec2, Touch, EventMouse, instantiate, director, BoxCollider2D, Collider2D, Collider, physics, PhysicsSystem, BoxCollider, PolygonCollider2D, Contact2DType } from 'cc';
import ReaveldCard from './ReaveldCard';
const { ccclass, property } = _decorator;

@ccclass('WinPile')
export class WinPile extends Component {

    start() {

        let winCollider = this.node.getComponent(PolygonCollider2D)
        winCollider.on(Contact2DType.BEGIN_CONTACT, this.onContact, this)
    }
    onContact(self: PolygonCollider2D, other: BoxCollider2D) {
        console.log("self", self)
        console.log("other", other)

    }

}

